.profile__avatar-wrapper {
  padding: 0;
  border: 0;
  margin: 0 30px 0 0;
  border-radius: 50%;
  overflow: hidden;
  display: flex;
  position: relative;
  cursor: pointer;
}

.profile__avatar-wrapper::before {
  content: '';
  width: 100%;
  height: 100%;
  background: url(../../../images/edit-button.svg) center/26px no-repeat, #000;
  opacity: 0;
  position: absolute;
  transition: opacity 0.25s ease-out, transform 0.1s ease;
}

.profile__avatar-wrapper:hover::before {
  opacity: 0.8;
}

.profile__avatar-wrapper:active::before {
  transform: scale(1.5);
}

@media screen and (max-width: 800px) {
  .profile__avatar-wrapper {
    margin: 0 0 26px 0;
  }
}